/* RESET SNIPPET ABOVE */

/************************************* CODE BEGINS BELOW ********************************/

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Hind Siliguri", sans-serif;
  font-size: 25px;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  width: 80%;
}

header {
  background-image: url("./assets/watermelon.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  padding: 5%;
}

header li:first-child {
  font-size: 35px;
  text-decoration: underline;
  margin-bottom: 3%;
  font-family: "Poiret One", cursive;
}

header img {
  width: 4%;
}

header li {
  margin-bottom: 1%;
}

header img:hover {
  background-color: rgba(141, 62, 62, 0);
}

header p {
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;
}

form {
  background-color: #e0f8d1;
  padding: 5%;
}

form h2 {
  margin-bottom: 5%;
}

form p {
  background-color: rgba(255, 255, 255, 0);
}

label {
  font-size: 35px;
}

select:focus-within,
input:focus-within {
  background-color: #f05f77;
}

option,
input,
select {
  padding: 1%;
  font-family: "Hind Siliguri", sans-serif;
}

input,
select,
option {
  margin-top: 4%;
}

.flexContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  text-align: center;
  font-size: 90px;
  background-color: #f05f77;
  border-radius: 45px;
  padding: 1%;
  width: 100%;
  margin: 5% 0 5% 0;
  font-family: "Poiret One", cursive;
}

h2 {
  text-align: center;
  font-size: 40px;
  background-color: #f05f77;
  border: 2px solid #000;
  padding: 2%;
  width: 100%;
  font-family: "Poiret One", cursive;
}

a {
  text-decoration: none;
  color: black;
  text-align: center;
}

p,
ul {
  margin-bottom: 5%;
  background-color: #fff6f7;
  padding: 5%;
}

.podcast {
  display: flex;
  justify-content: center;
  background-color: #fff6f7;
  padding: 5%;
  border-radius: 45px;
}

section {
  width: 100%;
}

/* section 2 */
.formBackground {
  background: url("./assets/watermelonBackground.jpg");
  background-size: cover;
  padding: 5%;
}

/* section 3 */
.mapBackground {
  background-color: #e0f8d1;
  padding: 5%;
}

/* section 4 */
.podcastBackground {
  background-image: url("./assets/watermelonBackground4.jpg");
  background-size: contain;
  padding: 5%;
}

.podcastBackground ul {
  background-color: rgba(255, 255, 255, 0);
}

input,
select {
  background-color: #fff6f7;
  width: 100%;
  margin-bottom: 2%;
}

button {
  background-color: #f05f77;
  padding: 2%;
  margin-top: 1%;
  border: 2px solid #000;
}

.transportationMode img {
  width: 100%;
  background-color: #f05f77;
  border: 2px solid #000;
}

button:hover,
button:focus,
button:focus-within,
img:hover,
img:focus,
img:focus-within {
  background-color: #ffaab8;
  cursor: pointer;
}

li {
  list-style: none;
}

.transportationMode ul {
  background-color: rgba(255, 255, 255, 0);
  width: 50%;
}

.transportationMode li:first-child {
  background-color: rgba(255, 255, 255, 0);
}

.transportationMode li {
  background-color: #fff6f7;
  padding: 2% 0 2% 0;
  text-align: center;
}

.flexContainer2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.flexContainer3 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.podcastSection li {
  background-color: #fff6f7;
  padding: 3%;
  text-align: center;
  width: 40%;
  margin: 5%;
}

.podcastSection a:hover,
.podcastSection a:focus,
.podcastSection a:focus-within {
  color: #f05f77;
}

.podcastSection h2 {
  margin-bottom: 8%;
}

.borderBox1 {
  border: 2px solid #000;
  padding: 5%;
  margin: 2%;
}

.chosenPodcast {
  padding: 5%;
  background-color: #fff6f7;
}

.chosenPodcast h2 {
  margin-bottom: 2%;
}

.chosenPodcast img {
  width: calc(500px - 50px);
  margin: 25px;
}

audio {
  margin-top: 10%;
}

footer {
  position: relative;
  bottom: 0;
  background-color: #fff6f7;
  text-align: center;
}

footer p {
  margin-bottom: 0;
  padding: 3%;
  font-size: 25px;
  line-height: 30px;
}

footer a:hover,
footer a:focus,
footer a:focus-within {
  color: #f05f77;
}

/* ******************************** MEDIA QUERIES *********************************** */

/* Tablet screen size */

@media (max-width: 870px) {
  .flexContainer3 {
    flex-direction: column;
  }

  .flexContainer3 li {
    width: 100%;
  }
}

/* Mobile screen size */

@media (max-width: 518px) {
  header h1 {
    font-size: 60px;
  }

  h2 {
    font-size: 20px;
  }

  header p {
    font-size: 20px;
  }

  header li {
    font-size: 20px;
    margin-bottom: 5%;
    line-height: 30px;
  }

  header img {
    width: 10%;
  }

  label,
  option,
  select {
    font-size: 20px;
  }

  input {
    font-size: 15px;
    padding: 5%;
  }

  .podcastSection h2,
  .chosenPodcast h2,
  .chosenPodcast p,
  .chosenPodcast a,
  footer p {
    font-size: 18px;
  }

  .chosenPodcast .flexContainer2 {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .chosenPodcast img {
    width: 100%;
  }

  .transportationMode .flexContainer2,
  .transportationMode ul {
    flex-direction: column;
    width: 100%;
  }
}

/* Small mobile screen size */

@media (max-width: 400px) {
  audio {
    width: 90%;
  }
}
